import React from 'react';

const UserDetail = async ({ params }) => {
    const { id } = params;
    const response = await fetch(`https://dummyjson.com/users/${id}`);
    const user = await response.json();
  
    if (!user) return <div>Загрузка...</div>;
  
    return (
      <div className="container mx-auto p-4">
        <div className="bg-white shadow-md rounded-lg p-6">
          <div className="flex items-center mb-4">
            <img src={user.image} alt={`${user.firstName} ${user.lastName}`} className="w-32 h-32 rounded-full mr-4 object-cover" />
            <div>
              <h1 className="text-2xl font-semibold text-gray-800">
                {user.firstName} {user.lastName} {user.maidenName}
              </h1>
              <p className="text-gray-600">{user.email}</p>
            </div>
          </div>
          <hr className="border-t-2 border-gray-300 my-4" />
          <br></br>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <p><strong>Age:</strong> {user.age}</p>
            <p><strong>Gender:</strong> {user.gender}</p>
            <p><strong>Phone:</strong> {user.phone}</p>
            <p><strong>Username:</strong> {user.username}</p>
            <p><strong>Password:</strong> {user.password}</p>
            <p><strong>Birth Date:</strong> {user.birthDate}</p>
            <p><strong>Blood Group:</strong> {user.bloodGroup}</p>
            <p><strong>Height:</strong> {user.height} см</p>
            <p><strong>Weight:</strong> {user.weight} кг</p>
            <p><strong>Eye Color:</strong> {user.eyeColor}</p>
            <p>
              <strong>Hair:</strong> {user.hair.color}, {user.hair.type}
            </p>
            <p>
              <strong>IP:</strong> {user.ip}
            </p>
            <p>
              <strong>Address:</strong> {user.address.address}, {user.address.city}, {user.address.state}, {user.address.stateCode}, {user.address.postalCode}, {user.address.coordinates.lat},  {user.address.coordinates.lng},  {user.address.country}
            </p>
            <p>
                <strong>MacAddress:</strong> {user.macAddress}
            </p>
            <p>
                <strong>University:</strong> {user.university}
            </p>
            <p>
                <strong>Bank:</strong> {user.bank.cardExpire}, {user.bank.cardNumber}, {user.bank.cardType}, {user.bank.currency}, {user.bank.iban}
            </p>
            <p>
                <strong>Company:</strong> {user.company.department}, "{user.company.name}", {user.company.title}, ({user.company.address.address}, {user.company.address.city}, {user.company.address.state}, {user.company.address.stateCode},{user.company.address.postalCode},{user.company.address.coordinates.lat},{user.company.address.coordinates.lng},{user.company.address.country})
            </p>
            <p>
                <strong>EIN:</strong> {user.ein}
            </p>
            <p>
                <strong>SSN:</strong> {user.ssn}
            </p>
            <p>
                <strong>UserAgent:</strong> {user.userAgent}
            </p>
            <p>
                <strong>Crypto:</strong> {user.crypto.coin}, {user.crypto.wallet}, {user.crypto.network}
            </p>
            <p>
                <strong>Role:</strong> {user.role}
            </p>
          </div>
        </div>
      </div>
    );
  };
  
  export default UserDetail;
  